.adaptive-menu {
  height: 100%;
}

.menu-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: -1;
}

@media screen and (max-width: 1000px) {
  .adaptive-menu {
    width: 0;
    z-index: 3;
  }
  .adaptive-menu .user-panel {
    right: -110px;
    top: 0;
    position: absolute;
    transition: right 0.5s ease-in-out;
  }
  .menu-icon.active + .adaptive-menu {
    display: flex;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 3;
    width: 100%;
    justify-content: end;
    min-height: calc(100vh - 136px);
  }
  .menu-icon.active + .adaptive-menu .user-panel {
    right: 0;
  }
  .menu-icon.active + .adaptive-menu .menu-overlay {
    display: block;
  }
}
@media screen and (max-width: 370px) {
  .menu-icon.active + .adaptive-menu {
    min-height: calc(100vh - 16px);
  }
}/*# sourceMappingURL=AdaptiveMenuBar.css.map */